"use client"

import { useState } from 'react'
import Link from 'next/link'
import CSVUpload from '@/components/CSVUpload'

interface InventoryItem {
  id: string
  manufacturer: string
  manufacturing_date: string
  condition: string
  transaction_date: string
  buy_date?: string
  repair_date?: string
  install_date?: string
  description?: string
}

export default function UploadPage() {
  const [uploadedData, setUploadedData] = useState<InventoryItem[] | null>(null)

  const handleUploadSuccess = (data: InventoryItem[]) => {
    setUploadedData(data)
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 p-4">
      <div className="max-w-2xl mx-auto pt-8">
        <div className="flex items-center mb-6">
          <Link href="/" className="text-blue-600 hover:text-blue-800 mr-4">
            ‚Üê Back to Home
          </Link>
          <h1 className="text-2xl font-bold text-gray-900">Upload Inventory Data</h1>
        </div>

        <CSVUpload onUploadSuccess={handleUploadSuccess} />

        {uploadedData && (
          <div className="mt-6 bg-white rounded-lg shadow-md p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Upload Summary</h3>
            <div className="grid grid-cols-2 gap-4 text-sm">
              <div>
                <span className="font-medium text-gray-700">Total Items:</span>
                <span className="ml-2 text-gray-900">{uploadedData.length}</span>
              </div>
              <div>
                <span className="font-medium text-gray-700">Manufacturers:</span>
                <span className="ml-2 text-gray-900">
                  {new Set(uploadedData.map(item => item.manufacturer)).size}
                </span>
              </div>
            </div>
            
            <div className="mt-4 flex space-x-3">
              <Link 
                href="/generate" 
                className="bg-green-600 text-white px-4 py-2 rounded-md hover:bg-green-700 transition-colors text-sm"
              >
                Generate QR Codes
              </Link>
              <Link 
                href="/scan" 
                className="bg-purple-600 text-white px-4 py-2 rounded-md hover:bg-purple-700 transition-colors text-sm"
              >
                Scan QR Code
              </Link>
            </div>
          </div>
        )}

        <div className="mt-6 bg-white rounded-lg shadow-md p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-3">Sample Data</h3>
          <p className="text-gray-600 text-sm mb-3">
            Don't have a CSV file? Download our sample data to test the application.
          </p>
          <a 
            href="/sample.csv" 
            download 
            className="inline-block bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition-colors text-sm"
          >
            Download Sample CSV
          </a>
        </div>
      </div>
    </div>
  )
}
