"use client"

import { useState } from 'react'
import Papa from 'papaparse'

interface InventoryItem {
  id: string
  manufacturer: string
  manufacturing_date: string
  condition: string
  transaction_date: string
  buy_date?: string
  repair_date?: string
  install_date?: string
  description?: string
}

interface CSVUploadProps {
  onUploadSuccess: (data: InventoryItem[]) => void
}

export default function CSVUpload({ onUploadSuccess }: CSVUploadProps) {
  const [isUploading, setIsUploading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [success, setSuccess] = useState<string | null>(null)

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (!file) return

    if (file.type !== 'text/csv' && !file.name.endsWith('.csv')) {
      setError('Please select a valid CSV file')
      return
    }

    setIsUploading(true)
    setError(null)
    setSuccess(null)

    Papa.parse(file, {
      header: true,
      skipEmptyLines: true,
      complete: (results) => {
        try {
          const data = results.data as InventoryItem[]
          
          // Validate required fields
          const requiredFields = ['id', 'manufacturer', 'manufacturing_date', 'condition', 'transaction_date']
          const firstRow = data[0]
          
          if (!firstRow) {
            throw new Error('CSV file is empty')
          }

          for (const field of requiredFields) {
            if (!(field in firstRow)) {
              throw new Error(`Missing required field: ${field}`)
            }
          }

          // Validate data integrity
          const validData = data.filter(item => 
            item.id && 
            item.manufacturer && 
            item.manufacturing_date && 
            item.condition && 
            item.transaction_date
          )

          if (validData.length === 0) {
            throw new Error('No valid data rows found')
          }

          // Store in localStorage
          localStorage.setItem('inventoryData', JSON.stringify(validData))
          
          setSuccess(`Successfully uploaded ${validData.length} items`)
          onUploadSuccess(validData)
          
        } catch (err) {
          setError(err instanceof Error ? err.message : 'Failed to parse CSV file')
        } finally {
          setIsUploading(false)
        }
      },
      error: (error) => {
        setError(`CSV parsing error: ${error.message}`)
        setIsUploading(false)
      }
    })
  }

  return (
    <div className="bg-white rounded-lg shadow-md p-6">
      <h2 className="text-xl font-semibold text-gray-900 mb-4">Upload Inventory CSV</h2>
      
      <div className="mb-4">
        <label htmlFor="csv-upload" className="block text-sm font-medium text-gray-700 mb-2">
          Select CSV File
        </label>
        <input
          id="csv-upload"
          type="file"
          accept=".csv"
          onChange={handleFileUpload}
          disabled={isUploading}
          className="block w-full text-sm text-gray-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100 disabled:opacity-50"
        />
      </div>

      {isUploading && (
        <div className="flex items-center text-blue-600 mb-4">
          <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600 mr-2"></div>
          Processing CSV file...
        </div>
      )}

      {error && (
        <div className="bg-red-50 border border-red-200 rounded-md p-3 mb-4">
          <p className="text-red-800 text-sm">{error}</p>
        </div>
      )}

      {success && (
        <div className="bg-green-50 border border-green-200 rounded-md p-3 mb-4">
          <p className="text-green-800 text-sm">{success}</p>
        </div>
      )}

      <div className="text-sm text-gray-600">
        <p className="mb-2">Required CSV columns:</p>
        <ul className="list-disc list-inside space-y-1 text-xs">
          <li>id (unique identifier)</li>
          <li>manufacturer</li>
          <li>manufacturing_date</li>
          <li>condition (brand new, used, repaired)</li>
          <li>transaction_date</li>
        </ul>
        <p className="mt-2 text-xs">
          Optional columns: buy_date, repair_date, install_date, description
        </p>
      </div>
    </div>
  )
}
